import React from "react";

function Nav({ setPage }) {
  return (
    <nav>
      <ul>
        <li>
          <a onClick={() => setPage("home")}>Home</a>
        </li>
        <li>
          <a onClick={() => setPage("gift")}>Gift</a>
        </li>
        <li>
          <a onClick={() => setPage("photo")}>Photo</a>
        </li>
        <li>
          <a onClick={() => setPage("song")}>Song</a>
        </li>
      </ul>
    </nav>
  );
}

export default Nav;
