import React, { useRef, useState, useEffect } from "react";
import Webcam from "react-webcam";
import "./photo.css";

function Photo() {
  const webcamRef = useRef(null);
  const canvasRef = useRef(null);
  const [imageSrc, setImageSrc] = useState(null);
  const [comment, setComment] = useState("");
  const [polaroidComment, setPolaroidComment] = useState("");

  const capture = () => {
    const imageSrc = webcamRef.current.getScreenshot();
    setImageSrc(imageSrc);
    setPolaroidComment("");
    setComment("");
  };

  const handleCommentChange = (e) => {
    setComment(e.target.value);
  };

  const handleAddComment = () => {
    setPolaroidComment(comment);
    drawPolaroid(imageSrc, comment);
  };

  const drawPolaroid = (imageSrc, comment) => {
    const canvas = canvasRef.current;
    const ctx = canvas.getContext("2d");
    const image = new Image();

    image.onload = () => {
      const padding = 20; // ÏúÑÏïÑÎûò Ïó¨Î∞±
      const imageHeight = 300;
      const canvasWidth = 300 + padding * 2; // Ïù¥ÎØ∏ÏßÄ ÎÑàÎπÑ + Ï¢åÏö∞ Ïó¨Î∞±
      const canvasHeight = imageHeight + padding * 3 + 20; // Ïù¥ÎØ∏ÏßÄ ÎÜíÏù¥ + ÏúÑÏïÑÎûò Ïó¨Î∞± + ÏΩîÎ©òÌä∏ ÏòÅÏó≠ ÎÜíÏù¥

      ctx.fillStyle = "white";
      ctx.fillRect(0, 0, canvasWidth, canvasHeight); // Ï†ÑÏ≤¥ Ìù∞ Î∞∞Í≤Ω
      ctx.drawImage(image, padding, padding, 300, 300); // Ïó¨Î∞± Ìè¨Ìï® Ïù¥ÎØ∏ÏßÄ ÏúÑÏπò Ï°∞Ï†ï

      ctx.fillStyle = "white";
      ctx.fillRect(padding, imageHeight + padding * 2, 300, padding); // ÏΩîÎ©òÌä∏ Î∞∞Í≤Ω Ìù∞ÏÉâÏúºÎ°ú ÏÑ§Ï†ï
      ctx.fillStyle = "#333";
      ctx.font = "16px Arial";
      ctx.fillText(comment, padding + 10, imageHeight + padding * 2 + 15); // Ïó¨Î∞± Ìè¨Ìï® ÏΩîÎ©òÌä∏ ÏúÑÏπò Ï°∞Ï†ï
    };

    image.src = imageSrc;
  };

  useEffect(() => {
    if (imageSrc) {
      drawPolaroid(imageSrc, polaroidComment);
    }
  }, [imageSrc, polaroidComment]);

  const downloadImage = () => {
    const canvas = canvasRef.current;
    const link = document.createElement("a");
    link.href = canvas.toDataURL("image/png");
    link.download = "polaroid.png";
    link.click();
  };

  return (
    <section>
      <h2>ÏÇ¨ÏßÑ Ï∞çÍ∏∞</h2>
      <Webcam
        audio={false}
        ref={webcamRef}
        screenshotFormat="image/png"
        width={300}
        height={400}
        videoConstraints={{
          width: 1280,
          height: 720,
          facingMode: "user",
        }}
      />
      <center>
        <button onClick={capture} class="custom-btn btn-6">
          <span>ÏÇ¨ÏßÑ Ï∞çÍ∏∞</span>
        </button>
      </center>
      <br></br>
      <center>
        {imageSrc && (
          <div
            style={{
              position: "relative",
              display: "inline-block",
              marginTop: "20px",
            }}
          >
            <canvas
              ref={canvasRef}
              width={340}
              height={400}
              style={{ display: "none" }}
            />
            <div
              style={{
                border: "10px solid white",
                padding: "10px",
                background: "white",
                boxShadow: "0 4px 8px rgba(0,0,0,0.1)",
                textAlign: "left",
                fontFamily: "Arial, sans-serif",
                color: "#333",
                marginTop: "20px",
                width: "320px",
                height: "420px",
              }}
            >
              <img
                src={imageSrc}
                alt="Captured"
                style={{ width: "100%", height: "80%", objectFit: "cover" }}
              />
              <div>{polaroidComment}</div>
            </div>
            <input
              type="text"
              value={comment}
              onChange={handleCommentChange}
              placeholder="Comment‚ù§Ô∏è"
            />

            <button onClick={handleAddComment}>Comment</button>
            <button onClick={downloadImage}>Îã§Ïö¥Î°úÎìú</button>
          </div>
        )}
      </center>
    </section>
  );
}

export default Photo;
