import React, { useState, useEffect, useRef } from "react";
import "./gift.css";

function Gift() {
  const [giftClicked, setGiftClicked] = useState(false);
  const [timeRemaining, setTimeRemaining] = useState("");
  const [isInitialRender, setIsInitialRender] = useState(true);
  const [isRed, setIsRed] = useState(false); // Ï∂îÍ∞Ä: ÏÉâÏÉÅ Î≥ÄÍ≤Ω ÏÉÅÌÉú

  const intervalRef = useRef(null);
  const blinkIntervalRef = useRef(null); // Ï∂îÍ∞Ä: ÍπúÎπ°ÏûÑ ÌÉÄÏù¥Î®∏

  useEffect(() => {
    const today = new Date();
    const birthday = new Date(today.getFullYear(), 6, 5); // 7Ïõî 5Ïùº
    // const birthday = new Date(today.getFullYear(), 6, 4, 7, 20); // test date 6Ïõî 28Ïùº
    if (today > birthday) {
      setTimeRemaining("0Ïùº 0ÏãúÍ∞Ñ 0Î∂Ñ 0Ï¥à");
      setIsInitialRender(false);
    } else {
      const updateTimer = () => {
        const now = new Date();
        const distance = birthday - now;
        if (distance < 0) {
          clearInterval(intervalRef.current);
          setTimeRemaining("0Ïùº 0ÏãúÍ∞Ñ 0Î∂Ñ 0Ï¥à");
        } else {
          const days = Math.floor(distance / (1000 * 60 * 60 * 24));
          const hours = Math.floor(
            (distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
          );
          const minutes = Math.floor(
            (distance % (1000 * 60 * 60)) / (1000 * 60)
          );
          const seconds = Math.floor((distance % (1000 * 60)) / 1000);

          setTimeRemaining(`${days}Ïùº ${hours}ÏãúÍ∞Ñ ${minutes}Î∂Ñ ${seconds}Ï¥à`);

          if (days === 0 && hours === 0 && minutes === 0 && seconds <= 5) {
            // ÎÇ®ÏùÄ ÏãúÍ∞ÑÏù¥ 5Ï¥à Ïù¥ÌïòÏùº Îïå ÍπúÎπ°ÏûÑ ÌÉÄÏù¥Î®∏ ÏÑ§Ï†ï
            if (!blinkIntervalRef.current) {
              blinkIntervalRef.current = setInterval(() => {
                setIsRed((prev) => !prev);
              }, 100); // 500msÎßàÎã§ ÏÉâÏÉÅ Î≥ÄÍ≤Ω
            }
          } else {
            // ÎÇ®ÏùÄ ÏãúÍ∞ÑÏù¥ 5Ï¥à Ïù¥ÏÉÅÏù¥Î©¥ ÍπúÎπ°ÏûÑ ÌÉÄÏù¥Î®∏ Ï†úÍ±∞
            if (blinkIntervalRef.current) {
              clearInterval(blinkIntervalRef.current);
              blinkIntervalRef.current = null;
              setIsRed(false);
            }
          }
        }
        setIsInitialRender(false); // Ï¥àÍ∏∞ Î†åÎçîÎßÅ ÏôÑÎ£å
      };
      intervalRef.current = setInterval(updateTimer, 1000);

      // Ï≤´ Î≤àÏß∏ ÌÉÄÏù¥Î®∏ Ïã§Ìñâ
      updateTimer();

      return () => {
        clearInterval(intervalRef.current);
        clearInterval(blinkIntervalRef.current);
      };
    }
  }, []);

  const handleGiftClick = () => {
    setGiftClicked(true);
  };

  const calculateTotalSecondsLeft = (timeString) => {
    const timeParts = timeString.split(/[ÏùºÏãúÍ∞ÑÎ∂ÑÏ¥à\s]+/).filter(Boolean);
    const [days, hours, minutes, seconds] = timeParts.map(Number);
    return days * 86400 + hours * 3600 + minutes * 60 + seconds;
  };

  const totalSecondsLeft = calculateTotalSecondsLeft(timeRemaining);

  return (
    <section>
      <h2>ÏÑ†Î¨º</h2>
      {!isInitialRender && (
        <p
          style={{
            color:
              totalSecondsLeft >= 0 && totalSecondsLeft <= 5 && isRed
                ? "red"
                : "black",
          }}
        >
          ÎÇ®ÏùÄ ÏãúÍ∞Ñ: {timeRemaining}
        </p>
      )}
      {totalSecondsLeft > 0 ? (
        <div>
          <img
            src={`${process.env.PUBLIC_URL}/giftBox.png`}
            alt="ÏÑ†Î¨º ÏÉÅÏûê ÌùîÎì§ÌùîÎì§ GIF"
          />
        </div>
      ) : (
        <div onClick={handleGiftClick} style={{ cursor: "pointer" }}>
          {giftClicked ? (
            <img
              src={`${process.env.PUBLIC_URL}/letter_image.png`}
              alt="Ìé∏ÏßÄ Ïù¥ÎØ∏ÏßÄ"
            />
          ) : (
            <img
              src={`${process.env.PUBLIC_URL}/explosion.png`}
              alt="ÌéëÌÑ∞ÏßÄÎäî GIF"
            />
          )}
        </div>
      )}
    </section>
  );
}

export default Gift;
